import React, {Component} from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import {OraButton, OraLoader} from "eshop/components/OraCommonComponents";
import {gotoPreviousCheckoutStep, openOrderCancelPopupFromNavComponent, cancelOrderWithRedirect, closeOrderCancelErrorPopup, verifyIdentity} from "../../actions/app";
import {validateData} from "../../actions/validation";
import {
    isDoingCheckoutStep,
    isNextStepAvailable,
    isOnlinePaymentMethodSelected,
    getSelectedPaymentMethod,
    reservationData,
    isIdVerificationRequiredAndNotVerified,
    getAgreementType, getSelectedDeliveryMethodCode
} from "../../selectors";
import {HideWhenCartIsEmpty, ShowWhenCartIsEmpty, HideWhenCartContainsEmptyEntries} from "eshop/modules/cart/components/Utils";
import {goBackEmptyCart, removeFromCart, createNewCart, removeFromCartAndRedirect, haltCart} from "eshop/modules/cart/actions/cart";
import OnlineUtils from "eshop/utils/OnlineUtils";
import MulticartCreateNewCartPopup from "../modals/MulticartCreateNewCartPopup";
import MulticartCancelOrderPopup from "../serialNumber/MulticartCancelOrderPopup";
import MulticartCancelOrderErrorPopup from "../serialNumber/MulticartCancelOrderErrorPopup";
import ModalService from "eshop/flux/utils/OraModalService";
import {getCartCheckoutCost, getEntryType} from "../../../cart/selectors";
import CartEntryType from "../../constants/CartEntryTypeEnum";
import DeliveryMethod from "../../constants/DeliveryMethod";


function generateUniqueKey(prefix){
    return OnlineUtils.generateUniqeHtmlId(prefix + "_");
}

const preventDefaultWrapper = (handler) => (event) => {
        event.preventDefault();
        handler(event);
    }

export class CheckoutNavigationComponentView extends Component {


    componentDidUpdate () {
        ModalService.add(() => <MulticartCreateNewCartPopup id={"create-new-cart-modal"}
                                                            onClickedCreateNew={this.props.onClickNew}/>);
        ModalService.add(() => <MulticartCancelOrderPopup {...this.props} id="cancel-order-popup-navcomponent"
                                                                   onConfirm={(this.props.reservation.sapReservationNumber && !this.props.reservation.paymentStatus) ?
                                                                                this.props.onClickCancelOrderAndRedirect : this.props.onClickRemoveAndRedirect}
                                                                   onClose={this.props.onClickOpenOrderCancelPopup.bind(this, false)}
                                                                   showWarning={false}
                                                                   cancelOrderQuestion={getCancelOrderQuestionText(this.props)}
                                                                   openPopup={this.props.reservation.cancelOrderPopupFromNavComponent}/>);
        ModalService.add(() => <MulticartCancelOrderErrorPopup {...this.props} id="cancel-error-order-popup-navcomponent"
                                                                   onConfirm={this.props.onClickCloseOrderCancelErrorPopup}/>);
    }
    render () {
    let msgClassName = (!this.props.showRemoveButton && !this.props.showBackButton && !this.props.showResignButton)
        ?   "l-col l-col-medium-5 l-col-6 u-small-hide u-text-right u-vertical-middle u-table-cell"
        :   "l-col l-col-small-12 l-col-medium-3 l-col-6 u-table-cell u-vertical-middle u-small-hide u-text-right";
    return (
    <div>
        <HaltCartComponent show={this.props.showHaltButton} label={this.props.descriptions.haltButtonLabel} action={this.props.haltCart}/>
        <div key = {generateUniqueKey(React.Component.name)} className="l-row">
            <div className="u-table u-w-100 u-table-fixed">
                    {this.props.showRemoveButton &&
                        [<ShowWhenCartIsEmpty key = {generateUniqueKey(React.Component.name)} className="l-col l-col-small-12 l-col-medium-3 l-col-2 u-vertical-middle u-small-hide">
                                                  <OraButton className="u-w-100" size="small" type="secondary" onClick={this.props.onClickReturn}>
                                                      {this.props.previousStepName}
                                                  </OraButton>
                        </ShowWhenCartIsEmpty>,
                        <HideWhenCartIsEmpty key = {generateUniqueKey(React.Component.name)} className="l-col l-col-small-12 l-col-medium-3 l-col-2  u-table-cell u-vertical-middle u-small-hide">
                             <OraButton className="u-w-100" size="small" type="secondary" onClick={this.props.onClickReturn}>
                                 {this.props.previousStepName}
                             </OraButton>
                        </HideWhenCartIsEmpty>,
                        <HideWhenCartIsEmpty key = {generateUniqueKey(React.Component.name)} className="l-col l-col-small-12 l-col-medium-3 l-col-2  u-table-cell u-vertical-middle u-small-hide">
                             <OraButton className="u-w-100" size="small" type="secondary"
                                        onClick={this.props.channels.sales === 'WWW' ? this.props.onClickRemove : this.props.onClickOpenOrderCancelPopup.bind(this, true)}>
                                 {(this.props.channels.sales === 'WWW' ? this.props.descriptions.removeCartButtonWWW : this.props.descriptions.removeCartButton) || "Wyczyść koszyk"}
                             </OraButton>
                         </HideWhenCartIsEmpty>
                        ]
                    }
                    {(!this.props.showRemoveButton && !this.props.showBackButton && this.props.showResignButton) &&  !(this.props.currentStepId==='delivery-payment') &&
                        [<HideWhenCartIsEmpty key = {generateUniqueKey(React.Component.name)} className="l-col l-col-small-12 l-col-medium-3 l-col-2  u-table-cell u-vertical-middle u-small-hide">
                             <OraButton className="u-w-100" size="small" type="secondary" onClick={this.props.onClickPrevious}>
                                 {this.props.previousStepName}
                             </OraButton>
                        </HideWhenCartIsEmpty>,
                       <HideWhenCartIsEmpty key = {generateUniqueKey(React.Component.name)} className="l-col l-col-small-12 l-col-medium-3 l-col-2  u-table-cell u-vertical-middle u-small-hide">
                             <OraButton className="u-w-100" size="small" type="secondary"
                                        onClick={this.props.channels.sales === 'WWW' ? this.props.onClickRemoveAndRedirect : this.props.onClickOpenOrderCancelPopup.bind(this, true)}>
                                 {this.props.descriptions.removeCartButton || "Rezygnacja z zamówienia"}
                             </OraButton>
                         </HideWhenCartIsEmpty>
                        ]
                    }
                    {(!this.props.showRemoveButton && !this.props.showBackButton && this.props.showResignButton) &&  (this.props.currentStepId==='delivery-payment') &&
                        [
                       <HideWhenCartIsEmpty key = {generateUniqueKey(React.Component.name)} className="l-col l-col-small-12 l-col-medium-3 l-col-2  u-table-cell u-vertical-middle u-small-hide">
                             <OraButton className="u-w-100" size="small" type="secondary"
                                        onClick={this.props.channels.sales === 'WWW' ? this.props.onClickRemoveAndRedirect : this.props.onClickOpenOrderCancelPopup.bind(this, true)}>
                                 {this.props.descriptions.removeCartButton || "Rezygnacja z zamówienia"}
                             </OraButton>
                         </HideWhenCartIsEmpty>
                        ]
                    }
                    {(!this.props.showRemoveButton && !this.props.showBackButton && !this.props.showResignButton) && (this.props.currentStepId!='delivery-payment') &&
                        [<div key = {generateUniqueKey(React.ElementType)} className="l-col l-col-medium-2 l-col-2 u-small-hide u-vertical-middle u-table-cell">
                             <OraButton className="u-w-100" size="small" type="secondary" onClick={this.props.onClickPrevious}>
                                 {this.props.previousStepName}
                             </OraButton>
                        </div>
                        ]
                    }

                    <HideWhenCartIsEmpty key={generateUniqueKey(React.Component.name)} className="l-col l-col-medium-2 l-col-2 u-small-hide u-vertical-middle u-padding-top-l u-padding-l">
                        {
                            this.props.showNewButton == 'true' && !this.props.showBackButton &&
                                <OraButton className="u-w-100" size="small" type="secondary" onClick={this.props.onCreateNewCartClicked}>
                                    {this.props.descriptions.newCartButton || "Nowy koszyk"}
                                </OraButton>
                        }
                    </HideWhenCartIsEmpty>

                    {!this.props.showBackButton  ?
                    [
                        (!(this.props.currentStepId==='delivery-payment')) ?
                            (<HideWhenCartIsEmpty key = {generateUniqueKey(React.Component.name)} className={msgClassName}>
                                <span>
                                    {this.props.descriptions.nextStepLabel || "Kolejny krok"}: <br/><span className="u-font-bold">{this.props.nextStepName}</span>
                                </span>
                            </HideWhenCartIsEmpty>) :

                            [<HideWhenCartIsEmpty  key = {generateUniqueKey(React.Component.name)} className="l-col l-col-small-12 l-col-medium-9 l-col-10  u-table-cell u-vertical-middle u-small-hide u-text-right">
                             <a href="#" onClick={
                                    preventDefaultWrapper(this.props.onClickPrevious)
                                } aria-label="wróć">Wróć do danych zamawiającego
                              </a>
                            </HideWhenCartIsEmpty>
                            ]
                        ,
                        <HideWhenCartIsEmpty key = {generateUniqueKey(React.Component.name)} className="l-col l-col-small-12 l-col-medium-3 l-col-2  u-table-cell u-vertical-middle u-small-block u-small-margin-l" >
                            <HideWhenCartContainsEmptyEntries key={generateUniqueKey(React.ElementType)}>
                                <OraLoader loading={this.props.loading} >
                                    <OraButton key = {generateUniqueKey(React.ElementType)} id="navigation-next-ora-button" className="u-w-100 u-right" disabled={this.props.disabledByDefault && this.props.disabled} onClick={this.onClickNext.bind(this)}>
                                        <span dangerouslySetInnerHTML={{__html: this.calculateNextButtonDescription()}}/>
                                    </OraButton>
                                </OraLoader>
                            </HideWhenCartContainsEmptyEntries>
                        </HideWhenCartIsEmpty>
                        ,
                        (this.props.currentStepId==='delivery-payment') ?
                        <HideWhenCartIsEmpty  key = {generateUniqueKey(React.Component.name)} className="l-col l-col-12  u-medium-hide u-large-hide">
                         <a href="#" onClick={
                                preventDefaultWrapper(this.props.onClickPrevious)
                            } aria-label="wróć">Wróć do danych zamawiającego
                          </a>
                        </HideWhenCartIsEmpty> : ""
                    ]
                    :
                        <div key = {generateUniqueKey(React.Component.name)}>
                            <div>
                                <OraButton className={"u-w-40 u-large-hide u-medium-hide" + (this.props.showBackButtonCms ? "" : " u-small-hide")} size="small" type="text"
                                        onClick={this.props.onClickReturn}>
                                    {this.props.descriptions.returnToShopButton || "Wróć do sklepu"}
                                </OraButton>
                            </div>
                            <div key = {generateUniqueKey(React.Component.name)}>
                                <OraButton className={"u-w-40 u-spacing-top-l u-small-hide" + (this.props.showBackButtonCms ? "" : " u-large-hide u-medium-hide")} size="small" type="secondary"
                                        onClick={this.props.onClickReturn}>
                                    {this.props.descriptions.returnToShopButton || "Wróć do sklepu"}
                                </OraButton>
                            </div>
                        <div className="u-spacing-l"/>
                    </div>
                    }
            </div>
        </div>
    </div>
    );
};

    calculateNextButtonDescription(){
        if (this.props.agreementType === "DIGITAL" && this.props.isIdVerificationRequiredAndNotVerified) {
            return this.props.descriptions.verifyIdentityButtonTitle || "Zweryfikuj tożsamość";
        }
        if(this.props.isOnlinePaymentMethodSelected){
            return this.props.descriptions.sogPayUNextButton;
        }
        console.log("this.props.checkoutCost ", this.props.checkoutCost);
        if (this.props.agreementType === "DIGITAL") {
            if (this.props.checkoutCost && this.props.checkoutCost.price > 0) {
                return this.props.descriptions.pickupOnEmailWithPayNowNextButton || "Zamawiam z obowiązkiem zapłaty";
            } else {
                return this.props.descriptions.pickupOnEmailNextButton || "Zawieram e-umowę i&nbsp;płacę";
            }
        }

        return this.props.currentStepId === 'delivery-payment' ? (this.props.descriptions.cashNextButton || "Potwierdzenie") : (this.props.descriptions.nextStepButton || "Dalej");
    }
    onClickNext() {
        if(this.props.isIdVerificationRequiredAndNotVerified) {
            this.props.verifyIdentity();
        }else{
            this.props.onClickNext();
        }
    }
}

function getCancelOrderQuestionText(props) {
    let msg = props.descriptions.orderCancellationText1 || "Czy na pewno chcesz zrezygnować z zamówienia i wyczyścić koszyk?";

    if (props.reservation.sapReservationNumber) {
        if (props.hasPosWarehouseman) {
            !props.reservation.paymentStatus ?
                msg = msg + " " + (props.descriptions.orderCancellationText2 || "Pamiętaj o zwrocie towaru do magazyniera w celu uwolnienia rezerwacji.") :
                msg = msg + " " + (props.descriptions.orderCancellationText3 || "Pamiętaj o konieczności wystawienia faktury korygującej oraz o zwrocie towaru do magazyniera w celu uwolnienia rezerwacji.");
        } else if (props.reservation.paymentStatus){
                msg = msg + " " + (props.descriptions.orderCancellationText4 || "Pamiętaj o konieczności wystawienia faktury korygującej.");
        }
    }
    return msg;
}

CheckoutNavigationComponentView.propTypes = {
    previousStepName: PropTypes.string,
    nextStepName: PropTypes.string,
    onClickPrevious: PropTypes.func,
    onClickNext: PropTypes.func,
    loading: PropTypes.bool,
    disabled: PropTypes.bool,
    backToShopUrl: PropTypes.string
}

const mapStateToProps = state => ({
    loading: isDoingCheckoutStep(state),
    disabled: !isNextStepAvailable(state),
    isOnlinePaymentMethodSelected: isOnlinePaymentMethodSelected(state),
    selectedPaymentMethod: getSelectedPaymentMethod(state),
    agreementType: getAgreementType(state),
    reservation: reservationData(state),
    checkoutCost: getCartCheckoutCost(state),
    isIdVerificationRequiredAndNotVerified: isIdVerificationRequiredAndNotVerified(state),
    entryType: getEntryType(state),
    haltingAvailable: CartEntryType.FIX !== getEntryType(state) || DeliveryMethod.ACCOUNT_MANAGER === getSelectedDeliveryMethodCode(state)
})

function getPreviousStepLabel(props) {
    return props.currentStepId === "cart-contents" ?
        props.descriptions.returnToShopButton || "Wróć do sklepu" :
        props.previousStepName;
}

const mapDispatchToProps = dispatch => ({
    onClickPrevious: () => dispatch(gotoPreviousCheckoutStep()),
    onClickNext: () => dispatch(validateData()),
    onClickRemove: () => dispatch(removeFromCart(null)),
    onClickRemoveAndRedirect: () => dispatch(removeFromCartAndRedirect(null)),
    onClickCancelOrderAndRedirect: () => dispatch(cancelOrderWithRedirect()),
    onClickOpenOrderCancelPopup: (param) => dispatch(openOrderCancelPopupFromNavComponent(param)),
    onClickCloseOrderCancelErrorPopup: () => dispatch(closeOrderCancelErrorPopup()),
    onClickLightLogout: () => dispatch(lightLogout()),
    onClickReturn: () => dispatch(goBackEmptyCart()),
    onClickNew: () => dispatch(createNewCart()),
    onCreateNewCartClicked: () => ModalService.open("create-new-cart-modal"),
    verifyIdentity: () => dispatch(verifyIdentity()),
    haltCart: () => dispatch(haltCart())
})

const mergeProps = (stateProps, dispatchProps, ownProps) => ({
    ...ownProps,
    ...stateProps,
    ...dispatchProps,
    showRemoveButton: ownProps.currentStepId === "cart-contents",
    showResignButton: (ownProps.currentStepId === "customer-data" || ownProps.currentStepId === "delivery-payment") && ownProps.channels.sales !== 'WWW',
    previousStepName: getPreviousStepLabel(ownProps),
    showBackButton: ownProps.currentStepId === "thank-you",
    showHaltButton: ownProps.currentStepId === "delivery-payment" && ownProps.haltingIsPossible && stateProps.haltingAvailable
})

const CheckoutNavigationComponent = connect(
    mapStateToProps,
    mapDispatchToProps,
    mergeProps
)(CheckoutNavigationComponentView)

export default CheckoutNavigationComponent;

class HaltCartComponent extends Component {
    render(){
        if(this.props.show){
            return (
            <div className="l-row">
                <div className="u-table u-w-100 u-table-fixed u-padding">
                    <div className="l-col l-col-medium-6 l-col-9"></div>
                    <div className="l-col l-col-small-12 l-col-medium-6 l-col-3 u-table-cell u-vertical-middle u-small-block u-small-margin-l">
                        <button className="o-btn opl-btn opl-btn--primary opl-btn--medium u-w-100 u-right" onClick={this.props.action}>
                            <span>{this.props.label || 'Wstrzymaj zamówienie'}</span>
                        </button>
                    </div>
                </div>
            </div>)
        } else {
            return null
        }
    }
}